////
/// Components/Puzzle/Theme
/// Eggplant: green, green, purple
////

.Theme-Eggplant {
  .Face--typeTop {
    background-color: get-color(purple);

    &.draggable-source:hover {
      background-color: get-color(purple, dark);
    }
  }

  .Face--typeRight {
    background-color: get-color(green);

    &.draggable-source:hover {
      background-color: get-color(green, dark);
    }
  }

  .Face--typeLeft {
    background-color: get-color(green, light);

    &.draggable-source:hover {
      background-color: get-color(green);
    }
  }

  &.draggable-source:hover {
    .Face--typeTop {
      background-color: get-color(purple, dark);
    }

    .Face--typeRight {
      background-color: get-color(green, dark);
    }

    .Face--typeLeft {
      background-color: get-color(green);
    }
  }
}
