////
/// Components/Puzzle/Theme
/// Island: aqua, aqua, yellow
////

.Theme-Island {
  .Face--typeTop {
    background-color: get-color(yellow);

    &.draggable-source:hover {
      background-color: get-color(yellow, dark);
    }
  }

  .Face--typeRight {
    background-color: get-color(aqua);

    &.draggable-source:hover {
      background-color: get-color(aqua, dark);
    }
  }

  .Face--typeLeft {
    background-color: get-color(aqua, lighter);

    &.draggable-source:hover {
      background-color: get-color(aqua);
    }
  }

  &.draggable-source:hover {
    .Face--typeTop {
      background-color: get-color(yellow, dark);
    }

    .Face--typeRight {
      background-color: get-color(aqua, dark);
    }

    .Face--typeLeft {
      background-color: get-color(aqua);
    }
  }
}

.Theme-Island--altStyle {
  &:hover {
    .Face--typeTop {
      background-color: get-color(aqua);
    }

    .Face--typeRight {
      background-color: get-color(yellow);
    }

    .Face--typeLeft {
      background-color: get-color(yellow, light);
    }
  }

  &:active {
    .Face--typeTop {
      background-color: get-color(aqua, dark);
    }

    .Face--typeRight {
      background-color: get-color(yellow, dark);
    }

    .Face--typeLeft {
      background-color: get-color(yellow);
    }
  }
}
