////
/// Components
/// Error404
////

// Import `completed` Brand and Stars mixin
@import 'components/Brand/props';
@import 'components/Stars/props';

#Page404 {
  height: 100vh;

  main {
    height: 100%;
  }

  @media screen and (min-width: get-breakpoint(tablet)) {
    padding: 0;
  }
}

.Error404 {
  @include stars-parent-section;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  background-color: black;

  ::selection {
    color: black;
    background-color: get-color(brand, yellow);
  }

  .Content {
    margin-bottom: 0;
  }

  ///
  /// Logo override
  @include logo-completed-colors;

  .Brand:hover {
    @include logo-completed-animation;
  }
}
