////
/// Components/Features
/// Extensible props
////

@import 'components/Puzzle/puzzle-props';

$puzzle-width-extensible: $cube-width * 2.5;
$puzzle-height-extensible: $cube-height * 2.5;

// --- :hover variants
$extensible-face-duration: get-duration(faster);
$extensible-total-duration: $extensible-face-duration * 18;

// --- :active variants
$extensible-face-duration-active: get-duration(faster) - 20ms;
$extensible-total-duration--active: $extensible-face-duration-active * 18;
