////
/// Components/Features
/// Accessible keyframes
////

@keyframes accessible-wave-left {
  0%,
  50%,
  100% {
    background-color: get-color(red, light);
  }

  25% {
    background-color: get-color(yellow, light);
  }
}

@keyframes accessible-wave-right {
  0%,
  50%,
  100% {
    background-color: get-color(red);
  }

  25% {
    background-color: get-color(yellow);
  }
}

@keyframes accessible-wave-top {
  0%,
  50%,
  100% {
    background-color: get-color(yellow);
  }

  25% {
    background-color: get-color(red);
  }
}
