////
/// Components/Puzzle/Theme
/// Watermelon: euca, red, euca
////

.Theme-Watermelon {
  .Face--typeTop {
    background-color: get-color(euca);

    &.draggable-source:hover {
      background-color: get-color(euca, dark);
    }
  }

  .Face--typeRight {
    background-color: get-color(red);

    &.draggable-source:hover {
      background-color: get-color(red, dark);
    }
  }

  .Face--typeLeft {
    background-color: get-color(euca);

    &.draggable-source:hover {
      background-color: get-color(euca, dark);
    }
  }

  &.draggable-source:hover {
    .Face--typeTop {
      background-color: get-color(euca, dark);
    }

    .Face--typeRight {
      background-color: get-color(red, dark);
    }

    .Face--typeLeft {
      background-color: get-color(euca, dark);
    }
  }
}
