////
/// Components/Puzzle/Theme
/// Aurora: purple, purple, aqua
////

.Theme-Aurora {
  .Face--typeTop {
    background-color: get-color(aqua);

    &.draggable-source:hover {
      background-color: get-color(aqua, dark);
    }
  }

  .Face--typeRight {
    background-color: get-color(purple);

    &.draggable-source:hover {
      background-color: get-color(purple, dark);
    }
  }

  .Face--typeLeft {
    background-color: get-color(purple, light);

    &.draggable-source:hover {
      background-color: get-color(purple);
    }
  }

  &.draggable-source:hover {
    .Face--typeTop {
      background-color: get-color(aqua, dark);
    }

    .Face--typeRight {
      background-color: get-color(purple, dark);
    }

    .Face--typeLeft {
      background-color: get-color(purple);
    }
  }
}
