////
/// Components/Puzzle/Theme
/// Tropic: blue, blue, red
////

.Theme-Tropic {
  .Face--typeTop {
    background-color: get-color(red);

    &.draggable-source:hover {
      background-color: get-color(red, dark);
    }
  }

  .Face--typeRight {
    background-color: get-color(blue);

    &.draggable-source:hover {
      background-color: get-color(blue, dark);
    }
  }

  .Face--typeLeft {
    background-color: get-color(blue, lightest);

    &.draggable-source:hover {
      background-color: get-color(blue, lighter);
    }
  }

  &.draggable-source:hover {
    .Face--typeTop {
      background-color: get-color(red, dark);
    }

    .Face--typeRight {
      background-color: get-color(blue, dark);
    }

    .Face--typeLeft {
      background-color: get-color(blue, lighter);
    }
  }
}

.Theme-Tropic--altStyle {
  &:hover {
    .Face--typeTop {
      background-color: get-color(blue);
    }

    .Face--typeRight {
      background-color: get-color(red);
    }

    .Face--typeLeft {
      background-color: get-color(red, light);
    }
  }

  &:active {
    .Face--typeTop {
      background-color: get-color(blue, dark);
    }

    .Face--typeRight {
      background-color: get-color(red, dark);
    }

    .Face--typeLeft {
      background-color: get-color(red);
    }
  }
}
