////
/// Components/Drag
/// Sortable
////

@import 'utils/shared/layout';
@import 'components/Puzzle/puzzle-props';
@import 'props';

.Sortable {
  padding-top: get-spacing(fat);
  background-color: get-color(red);

  @media screen and (min-width: get-breakpoint(tablet)) {
    padding-top: get-spacing(huger);
  }

  @media screen and (min-width: get-breakpoint(tablet, wide)) {
    padding-top: get-spacing(hugest);
  }

  ::selection {
    color: get-color(red);
  }
}

///
/// Puzzle layout
.Puzzle--idSortable {
  width: $puzzle-width-sortable;
  height: $puzzle-height-sortable - $puzzle-offset-y-sortable;

  .PuzzleInterior {
    height: $puzzle-height-sortable;
  }
}

///
/// States
.Puzzle--idSortable {
  .draggable-source {
    transition: transform get-duration() get-easing(excite);

    // required to reset, otherwise it affects the mouse movement
    &.draggable-mirror {
      transition: none;
    }
  }

  .draggable-source--is-dragging {
    .Face {
      background-color: transparent;

      .Svg {
        opacity: 0;
      }
    }
  }
}

///
/// Cube layout
.Sortable {
  @include draggable-original-layout('.Cube', 1) {
    @include cube-layout(3, -5);
  }

  @include draggable-original-layout('.Cube', 2) {
    @include cube-layout(2, -6);
  }

  @include draggable-original-layout('.Cube', 3) {
    @include cube-layout(1, -7);
  }

  @include draggable-original-layout('.Cube', 4) {
    @include cube-layout(0, -8);
  }

  @include draggable-original-layout('.Cube', 5) {
    @include cube-layout(3, -3);
  }

  @include draggable-original-layout('.Cube', 6) {
    @include cube-layout(3, -1);
  }

  @include draggable-original-layout('.Cube', 7) {
    @include cube-layout(3, 1);
  }

  @include draggable-original-layout('.Cube', 8) {
    @include cube-layout(2);
  }

  @include draggable-original-layout('.Cube', 9) {
    @include cube-layout(1, -1);
  }

  @include draggable-original-layout('.Cube', 10) {
    @include cube-layout(0, -6);
  }

  @include draggable-original-layout('.Cube', 11) {
    @include cube-layout(0, -4);
  }

  @include draggable-original-layout('.Cube', 12) {
    @include cube-layout(0, -2);
  }
}
