@import 'components/Puzzle/puzzle-props';

$puzzle-offset-y-collidable: $cube-height;
$puzzle-height-collidable: $cube-height * 3.5;
$puzzle-shadow-left-width-collidable: ($cube-width * 3) + $puzzle-supplement-offset;
$puzzle-shadow-right-width-collidable: ($cube-width * 2) + $puzzle-supplement-offset;
