////
/// Components/Puzzle/Theme
/// Mana: purple, purple, blue
////

.Theme-Mana {
  .Face--typeTop {
    background-color: get-color(blue);

    &.draggable-source:hover {
      background-color: get-color(blue, dark);
    }
  }

  .Face--typeRight {
    background-color: get-color(purple);

    &.draggable-source:hover {
      background-color: get-color(purple, dark);
    }
  }

  .Face--typeLeft {
    background-color: get-color(purple, light);

    &.draggable-source:hover {
      background-color: get-color(purple);
    }
  }

  &.draggable-source:hover {
    .Face--typeTop {
      background-color: get-color(blue, dark);
    }

    .Face--typeRight {
      background-color: get-color(purple, dark);
    }

    .Face--typeLeft {
      background-color: get-color(purple);
    }
  }
}
