////
/// Components
/// Content
////

@import 'utils/shared/layout';
@import 'props';

.Content {
  @include centered-width(get-layout-length(content));
  margin-bottom: get-spacing(loose);
  text-align: center;

  @media screen and (min-width: get-breakpoint(tablet)) {
    margin-bottom: get-spacing(fat);
  }

  @media screen and (min-width: get-breakpoint(tablet, wide)) {
    margin-bottom: get-spacing(fattest);
  }

  // --- Typographic elements
  p {
    @include ContentParagraph;
  }
}

.Content--sizeSmall {
  max-width: get-layout-length();
}

.HelperText {
  @include HelperText;
}

.HelperBullet {
  margin-right: $spacing-unit;
  margin-left: $spacing-unit;
}
