////
/// Components
/// Stars keyframes
////

@import 'props';

@keyframes star-drift {
  to {
    transform: translate3d(0, -$star-pattern-size, 0);
  }
}

// Beware! Use of `random()` will mean the result changes every time I recompile the sass.
@for $i from 1 through $shooting-star-count {
  $start: random($shooting-star-delay-max);
  $middle: $start + $shooting-star-visible-duration;
  $end: $middle + $shooting-star-fade;

  @keyframes shooting-star-#{$i} {
    0%,
    #{$start * 1%} {
      opacity: 0;
      transform: translate3d(0, 0, 0) rotate($shooting-star-angle);
    }

    #{$middle * 1%} {
      opacity: 1;
    }

    #{$end * 1%},
    100% {
      opacity: 0;
      transform: translate3d($shooting-star-distance-x, -$shooting-star-distance-y, 0) rotate($shooting-star-angle);
    }
  }
}
