////
/// Components/Features
/// Animation
////

@import 'utils/shared/animation';
@import 'components/Puzzle/puzzle-props';
@import 'props';

.Puzzle--idAnimation {
  height: $puzzle-height-animation;
}

///
/// Cube layout
.Animation {
  .Cube:nth-child(1) {
    @include cube-layout(0, 1);
  }

  .Cube:nth-child(2) {
    @include cube-layout(4, 1);
  }

  .Cube:nth-child(3) {
    @include cube-layout(8, 1);
  }
}

///
/// Cube juice
.Puzzle--idAnimation {
  .Shadow,
  .Jelly {
    transition-property: opacity, transform;
    transition-duration: get-duration(slow);
    transition-timing-function: get-easing(bungie);
  }

  .Cube {
    cursor: get-cursor(rock);

    &:hover {
      .Shadow {
        opacity: $puzzle-shadow-opacity / 2.5;
      }

      .JellyInterior {
        @include jelly-animation;
      }

      @include animation-hover-styles($animation-cube-distance);

      @media screen and (min-width: get-breakpoint(tablet)) {
        @include animation-hover-styles($animation-cube-distance-tablet);
      }

      @media screen and (min-width: get-breakpoint(tablet, wide)) {
        @include animation-hover-styles($animation-cube-distance-desktop);
      }
    }

    &:active {
      @include animation-active-styles;
      cursor: get-cursor(rock, active);
    }
  }
}
