////
/// Components/Puzzle/Theme
/// Aquaman: blue, blue, purple
////

.Theme-Aquaman {
  .Face--typeTop {
    background-color: get-color(purple);

    &.draggable-source:hover {
      background-color: get-color(purple, dark);
    }
  }

  .Face--typeRight {
    background-color: get-color(brand-muted, blue-light);

    &.draggable-source:hover {
      background-color: get-color(blue);
    }
  }

  .Face--typeLeft {
    background-color: get-color(brand-muted, blue-lighter);

    &.draggable-source:hover {
      background-color: get-color(brand-muted, blue-light);
    }
  }

  &.isColliding,
  &.draggable-source:hover {
    .Face--typeTop {
      background-color: get-color(purple, dark);
    }

    .Face--typeRight {
      background-color: get-color(blue);
    }

    .Face--typeLeft {
      background-color: get-color(brand-muted, blue-light);
    }
  }
}
