////
/// Components
/// Content props
////

@mixin ContentParagraph {
  font-size: get-type-scale(paragraph);
  font-weight: 700;
  line-height: 1.6;

  @media screen and (min-width: get-breakpoint(tablet)) {
    font-size: get-type-scale(paragraph, tablet);
  }

  @media screen and (min-width: get-breakpoint(tablet, wide)) {
    font-size: get-type-scale(paragraph, tablet-wide);
  }
}

@mixin HelperText {
  display: block;
  margin-top: get-spacing(tight);
  font-size: get-type-scale(helper);
  font-weight: 600;
  color: white;

  @media screen and (min-width: get-breakpoint(tablet)) {
    margin-top: get-spacing();
    font-size: get-type-scale(helper, tablet);
  }
}
