////
/// Components/Puzzle/Theme
/// Toybox: yellow, yellow, blue
////

.Theme-Toybox {
  .Face--typeTop {
    background-color: get-color(blue);

    &.draggable-source:hover {
      background-color: get-color(blue, dark);
    }
  }

  .Face--typeRight {
    background-color: get-color(yellow);

    &.draggable-source:hover {
      background-color: get-color(yellow, dark);
    }
  }

  .Face--typeLeft {
    background-color: get-color(yellow, light);

    &.draggable-source:hover {
      background-color: get-color(yellow);
    }
  }

  &.draggable-source:hover {
    .Face--typeTop {
      background-color: get-color(blue, dark);
    }

    .Face--typeRight {
      background-color: get-color(yellow, dark);
    }

    .Face--typeLeft {
      background-color: get-color(yellow);
    }
  }
}

.Theme-Toybox--altStyle {
  &:hover {
    .Face--typeTop {
      background-color: get-color(yellow);
    }

    .Face--typeRight {
      background-color: get-color(blue);
    }

    .Face--typeLeft {
      background-color: get-color(blue, lighter);
    }
  }

  &:active {
    .Face--typeTop {
      background-color: get-color(yellow, dark);
    }

    .Face--typeRight {
      background-color: get-color(blue, dark);
    }

    .Face--typeLeft {
      background-color: get-color(blue, light);
    }
  }
}
