////
/// Components/Puzzle/Theme
/// Candy: red, red, yellow
////

.Theme-Candy {
  .Face--typeTop {
    background-color: get-color(yellow);

    &.draggable-source:hover {
      background-color: get-color(yellow, dark);
    }
  }

  .Face--typeRight {
    background-color: get-color(red);

    &.draggable-source:hover {
      background-color: get-color(red, dark);
    }
  }

  .Face--typeLeft {
    background-color: get-color(red, light);

    &.draggable-source:hover {
      background-color: get-color(red);
    }
  }

  &.isColliding,
  &.draggable-source:hover {
    .Face--typeTop {
      background-color: get-color(yellow, dark);
    }

    .Face--typeRight {
      background-color: get-color(red, dark);
    }

    .Face--typeLeft {
      background-color: get-color(red);
    }
  }
}
