////
/// Components/Puzzle/Theme
/// Berry: purple, purple, red
////

.Theme-Berry {
  .Face--typeTop {
    background-color: get-color(red);

    &.draggable-source:hover {
      background-color: get-color(red, dark);
    }
  }

  .Face--typeRight {
    background-color: get-color(purple);

    &.draggable-source:hover {
      background-color: get-color(purple, dark);
    }
  }

  .Face--typeLeft {
    background-color: get-color(purple, light);

    &.draggable-source:hover {
      background-color: get-color(purple);
    }
  }

  &.draggable-source:hover {
    .Face--typeTop {
      background-color: get-color(red, dark);
    }

    .Face--typeRight {
      background-color: get-color(purple, dark);
    }

    .Face--typeLeft {
      background-color: get-color(purple);
    }
  }
}
