////
/// Components
/// Brand
////

@import 'keyframes';
@import 'props';

.Brand {
  width: $wordmark-draggable-width;
  margin-right: auto;
  margin-bottom: get-spacing(tight);
  margin-left: auto;
  cursor: get-cursor(rock);

  @media screen and (min-width: get-breakpoint(tablet)) {
    width: $wordmark-draggable-width-tablet;
  }

  &:active {
    cursor: get-cursor(rock, active);
  }
}

.BrandLogo {
  width: $logo-draggable-width;
  margin-right: auto;
  margin-left: auto;

  @media screen and (min-width: get-breakpoint(tablet)) {
    width: $logo-draggable-width-tablet;
  }
}

.BrandWordmark {
  .Svg {
    fill: get-color(brand, yellow);
  }
}

///
/// Logo
@include logo-initial-colors;

.Wave,
.Hand {
  transition: fill get-duration(slow) get-easing();
}

// --- Interaction
.Brand {
  &:hover {
    @include logo-animation;
  }

  &:active {
    .Wave--colorMask,
    .Wave--colorPurple,
    .Wave--colorRed,
    .Wave--colorOrange,
    .Hand {
      animation-duration: get-duration();
    }
  }
}
