////
/// Components
/// Brand props
////

$logo-draggable-width: 4.6rem;
$logo-draggable-width-tablet: 6rem;
$logo-draggable-bounce-offset: 0.1rem;
$wordmark-draggable-width: 23.4rem;
$wordmark-draggable-width-tablet: 32rem;

@mixin logo-initial-colors {
  .Wave--colorMask {
    // transparent so it doesn't appear to instantly switch colors
    // when changing to the `completed` state
    fill: transparent;
  }

  .Wave--colorPurple {
    fill: get-color(brand-muted, purple);
  }

  .Wave--colorRed {
    fill: get-color(brand-muted, red);
  }

  .Wave--colorOrange {
    fill: get-color(brand-muted, orange);
  }

  .Hand {
    fill: get-color(brand, yellow);
  }
}

@mixin logo-completed-colors {
  .Wave--colorPurple {
    fill: get-color(brand, purple);
  }

  .Wave--colorRed {
    fill: get-color(brand, red);
  }

  .Wave--colorOrange {
    fill: get-color(brand, orange);
  }
}

@mixin logo-animation {
  .Wave--colorMask {
    animation: logo-rainbow-mask get-duration(slow) get-easing() infinite;
  }

  .Wave--colorPurple {
    animation: logo-rainbow-purple get-duration(slow) get-easing() infinite;
  }

  .Wave--colorRed {
    animation: logo-rainbow-red get-duration(slow) get-easing() infinite;
  }

  .Wave--colorOrange {
    animation: logo-rainbow-orange get-duration(slow) get-easing() infinite;
  }

  .Hand {
    animation: logo-outline-bounce get-duration(slow) linear infinite;
  }
}

@mixin logo-completed-animation {
  .Wave--colorMask {
    animation-name: logo-rainbow-mask-completed;
  }

  .Wave--colorPurple {
    animation-name: logo-rainbow-purple-completed;
  }

  .Wave--colorRed {
    animation-name: logo-rainbow-red-completed;
  }

  .Wave--colorOrange {
    animation-name: logo-rainbow-orange-completed;
  }
}
