////
/// Components
/// Stars
////

@import 'utils/shared/layout';
@import 'keyframes';
@import 'props';

///
/// Stars
.Stars {
  @include position-cover;
  overflow: hidden;
  background-blend-mode: screen, multiply;
  background: radial-gradient(
    ellipse at bottom,
    get-color(stars, haze) 0%,
    get-color(stars) 100%
  ), radial-gradient(
    ellipse at top right,
    get-color(stars, purple) 0%,
    get-color(stars, blue) 40%,
    get-color(stars, coal) 80%,
    get-color(stars) 100%
  );

  @media screen and (min-width: get-breakpoint(tablet)) {
    top: -$border-section-width-tablet;
    right: -$border-section-width-tablet;
    left: -$border-section-width-tablet;
  }

  .Error404 & {
    @media screen and (min-width: get-breakpoint(tablet)) {
      bottom: -$border-section-width-tablet;
    }
  }
}

///
/// Star fields
.StarField {
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  height: 175%;
  animation-name: star-drift;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
}

.StarField--sizeSmall {
  animation-duration: 20s;
}

.StarField--sizeMedium {
  animation-duration: 40s;
}

.StarField--sizeLarge {
  animation-duration: 60s;
}

///
/// Shooting Stars
.ShootingStar {
  position: absolute;
  width: $shooting-star-size;
  height: $shooting-star-size;
  opacity: 0;

  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    display: block;
    width: 100%;
    height: $shooting-star-tail-length;
    background: linear-gradient(
      0deg,
      rgba(get-color(stars, tail), 0),
      rgba(get-color(stars, tail), 1)
    );
  }

  .Error404 &,
  .Hero--isCompleted & {
    animation-duration: $shooting-star-duration;
    animation-timing-function: linear;
    animation-delay: get-duration(slowest);
    animation-iteration-count: infinite;
  }
}

// Beware! Use of `random()` will mean the result changes every time I recompile the sass.
// This will cause the styles.css content hash to change...
// which then means the ServiceWorker requires a refresh to fetch the new asset.
@for $i from 1 through $shooting-star-count {
  .ShootingStar--index#{$i} {
    top: random(100) * 1%;
    left: random(100) * 1%;

    .Error404 &,
    .Hero--isCompleted & {
      animation-name: shooting-star-#{$i};
    }
  }
}
