////
/// Components/Features
/// Extensible keyframes
////

///
/// :hover variants
@keyframes extensible-wave-left {
  0%,
  50%,
  100% {
    background-color: get-color(purple, light);
  }

  25% {
    background-color: get-color(purple, lighter);
  }
}

@keyframes extensible-wave-top {
  0%,
  50%,
  100% {
    background-color: get-color(blue);
  }

  25% {
    background-color: get-color(blue, light);
  }
}

@keyframes extensible-wave-right {
  0%,
  50%,
  100% {
    background-color: get-color(purple);
  }

  25% {
    background-color: get-color(purple, light);
  }
}

///
/// :active variants
@keyframes extensible-wave-left--active {
  0%,
  50%,
  100% {
    background-color: get-color(purple, light);
  }

  25% {
    background-color: get-color(purple, lightest);
  }
}

@keyframes extensible-wave-top--active {
  0%,
  50%,
  100% {
    background-color: get-color(blue);
  }

  25% {
    background-color: get-color(blue, lighter);
  }
}

@keyframes extensible-wave-right--active {
  0%,
  50%,
  100% {
    background-color: get-color(purple);
  }

  25% {
    background-color: get-color(purple, lighter);
  }
}
