////
/// Components/Puzzle/Theme
/// Swappable: aqua, yellow, red, purple
////

.Theme-Swappable {
  .Face--typeRight,
  .Face--typeLeft {
    background-color: get-color(aqua);
  }
}

.Theme-Swappable--colorYellow {
  .Face--typeTop {
    background-color: get-color(yellow);

    &:hover {
      // normal shift is too dark, so we use the SASS function instead
      background-color: darken(get-color(yellow), 8%);
    }
  }
}

.Theme-Swappable--colorRed {
  .Face--typeTop {
    background-color: get-color(red);

    &:hover {
      background-color: get-color(red, dark);
    }
  }
}

.Theme-Swappable--colorPurple {
  .Face--typeTop {
    background-color: get-color(purple);

    &:hover {
      background-color: get-color(purple, dark);
    }
  }
}
