////
/// Components/Features
/// Animation props
////

@import 'components/Puzzle/puzzle-props';

$puzzle-height-animation: $cube-height;
$animation-shadow-scale: 0.6;
$animation-cube-distance: get-spacing(tight);
$animation-cube-distance-tablet: get-spacing();
$animation-cube-distance-desktop: get-spacing(looser);

@mixin animation-hover-styles($distance) {
  .Shadow {
    transform: scale($animation-shadow-scale) rotateX($isometric-skew) translateY($distance * 1.5);
  }

  .Jelly {
    transform: translateY(-$distance);
  }
}

@mixin animation-active-styles {
  .Shadow {
    transition-duration: get-duration();
    opacity: $puzzle-shadow-opacity;
    transform: scale(0.9) rotateX(0) translateY(0);
  }

  .Jelly {
    transition-duration: get-duration();
    transform: scale(0.9, 0.85) translateY(1em);
  }
}
