////
/// Components
/// Stars props
////

$star-pattern-size: 60rem;
$shooting-star-count: 12;
$shooting-star-size: 0.1rem;
$shooting-star-tail-length: 10rem;
$shooting-star-distance-x: 10rem;
$shooting-star-distance-y: 14rem;
$shooting-star-angle: 30deg;
$shooting-star-duration: get-duration(slowest) * 6;
$shooting-star-delay-max: 80;
$shooting-star-visible-duration: 5;
$shooting-star-fade: 4;

@mixin stars-parent-section {
  position: relative; // needed for stars

  // make sure the index is higher than the stars
  .SectionInterior {
    position: relative;
    z-index: get-z-index();
  }
}
